/* - Boutons */
.at-btn-more,
.textwidget .at-btn-more {
  display: inline-block;
  margin: 0;
  padding: 8px 18px;
  line-height: 20px;
  text-align: center;
  vertical-align: middle;
  font-size: 16px;
  color: $color02;
  text-transform: uppercase;
  @extend .font-source-sans-bold;
  box-shadow: none;
  border-radius: 5px;
  border: 1px solid $color02;
  background-color: transparent;
  transition: all 0.3s ease-in-out 0s;

  .category-page-one-content & {
    border-color: $color10;
    background-color: $white;
  }
  &:hover,
  &:focus {
    color: $white;
    border-color: $color02;
    background-color: $color02;
  }
}
p.at-btn-more {
  padding: 0;

  a {
    display: inline-block;
    padding: 8px 18px;

    &:hover,
    &:focus {
      color: $white;
    }
  }
}


.at-btn-all {
  display: inline-block;
  margin: 0;
  padding: 8px 18px;
  line-height: 20px;
  text-align: center;
  vertical-align: middle;
  font-size: 16px;
  color: $white;
  text-transform: uppercase;
  @extend .font-source-sans-medium;
  box-shadow: none;
  border-radius: 5px;
  border: 1px solid $white;
  background-color: $color10;
  transition: all 0.3s ease-in-out 0s;
  &:hover,
  &:focus {
    color: $white;
    background-color: $color02;
  }
}


