/* - infos utiles */
.home-section-usefull-infos {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.hsui-picture,
.hsui-menu {
  flex: 1;
}
.hsui-picture {
  display: flex;
  justify-content: center;
}
.hsui-menu {
  display: flex;
  align-self: stretch;
  ul {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex: 1;
    margin: 10px 0 0 0;
    padding: 0;
    list-style-type: none;
  }
  li {
    padding: 0;
    a {
      position: relative;
      display: block;
      margin: 5px 0 ;
      padding: 6px 50px 6px 15px;
      color: $color06;
      font-size: 24px;
      line-height: 1.5;
      @extend .font-source-sans-regular;
      background-color: $color05;
      transition: all 0.3s ease-in-out 0s;
      &:after {
        position: absolute;
        top: 0;
        right: 0;
        display: block;
        height: 100%;
        width: 45px;
        content: '+';
        @extend .font-source-sans-bold;
        font-size: 35px;
        line-height: 1.5;
        color: $white;
        text-align: center;
        background-color: $color02;
        transition: all 0.3s ease-in-out 0s;
      }
      &:hover {
        &:after {
          background-color: $color01;
        }
      }
    }
  }
}



/* - Partenaires carousel */
.home-carousel-partners,
.home-carousel-partners .col-xs-4 {
  height: 100px;
}
.home-carousel-partners .col-xs-4 {
  position: relative;
  > img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    max-width: 100px;
    max-height: 100px;
  }
}
.multi-item-carousel {
  .carousel-inner {
    > .item {
      transition: 500ms ease-in-out left;
    }
    .active.left,
    .active.right {
      left: -33%;
    }
    .next,
    .prev {
      left: 33%;
    }
  }
  .carouse-control.left,
  .carouse-control.right {
    background-image: none;
  }
}
@media all and (transform-3d), (-webkit-transform-3d) {
  .multi-item-carousel .carousel-inner > .item {
    //transition: 500ms ease-in-out left;
    transition: 500ms ease-in-out all;
    backface-visibility: visible;
    transform: none !important;
  }
}

/*Homepage Carousel Bootstrap Slider Section------------------------------------------------------------------*/
.carousel-inner {
  text-align: center;
}
.slide-caption {
  width: 100%;

  p {
    color: $white;
    font-size: 12px;
    font-weight: bold;
